.gnb__cont {
  background-color: white;
  .inner_cont {
    .main_menus {
      color: black;
      .center {
        & > div {
          & > div {
            & > a {
              text-decoration: none;
              color: black;
            }
          }
        }
      }
    }
  }
}
.full-size {
  width: 100%;
}
.container {
  width: 1100px;
  margin: 0 auto;
}

.svg-icon {
  filter: none;
}

.contents {
  margin-top: 100px;
  .sub_nav {
    height: 60px;
    background-color: #007c91;
    & > .container {
      height: inherit;
      display: flex;
      flex-direction: row;
      & > .icon {
        height: inherit;
        width: 70px;
        border-left: 1px solid #ccc;
        display: flex;
        justify-content: center;
        & > img {
          filter: invert(1);
          width: 30px;
        }
      }
      .sb_main_menu {
        height: inherit;
        width: 160px;
        border-left: 1px solid #ccc;
        display: flex;
        align-items: center;
        padding: 0px 20px 0px 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        & :first-child {
          color: white;
          font-family: SDGB;
        }
        & .icon > img {
          padding-top: 2px;
          height: inherit;
          width: 20px;
          filter: invert(1);
        }
      }
      .sb_sub_menu {
        padding-top: 10px;
        height: inherit;
        width: 160px;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        display: flex;
        align-items: center;
        padding: 0px 20px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        & :first-child {
          color: white;
          font-family: SDGB;
        }
        & .icon > img {
          padding-top: 4px;
          height: inherit;
          width: 20px;
          filter: invert(1);
        }
      }
    }
  }
  .summary {
    height: 150px;
    .container {
      & > div {
        padding-top: 40px;
      }
    }
  }
  .img_back {
    height: 300px;
    background-image: url("../images/intro.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  .article {
    .container {
      height: 800px;
      margin-top: 60px;
      margin-bottom: 60px;
      .noti {
        text-align: center;
      }
      .image_cont {
        height: 300px;
        width: inherit;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .images {
          margin-top: 150px;
          display: flex;
          flex-direction: column;
          & > img {
            width: 280px;
            height: 280px;
            border-radius: 150px;
          }
          & > h3 {
            text-align: center;
          }
        }
      }
      .text_cont {
        margin-top: 150px;
        height: 150px;
        .mission {
          border-top: 1px solid #ccc;
          border-bottom: 1px solid #ccc;
          height: 130px;
          display: flex;
          margin: 10px 0px;
          align-items: center;
          flex-direction: row;
          & > .title {
            flex: 1;
            align-items: center;
            & > strong {
              color: #007c91;
            }
          }
          & > .text_contents {
            flex: 2.1;
            align-items: center;
          }
        }
        .vision {
          border-bottom: 1px solid #ccc;
          height: 130px;
          display: flex;
          margin: 10px 0px;
          flex-direction: row;
          align-items: center;
          & > .title {
            flex: 1;
            align-items: center;
            & > strong {
              color: #007c91;
            }
          }
          & > .text_contents {
            flex: 2.1;
            align-items: center;
          }
        }
      }
    }
  }
}
